  font-weight: 400;
  line-height: 1.375;
  color: #000000;
  background: #ffffff; }

p,
ul {
  margin: 0; }

.container {
  max-width: 75rem;
  margin: 0 auto;
  padding: 0 1.0625rem; }

/*============================================= */
/*=============================================
        ШАПКА
============================================= */
.header {
  height: 49.125rem;
  background-image: url(../img/images/dgHeader.png);
  background-repeat: no-repeat;
  background-position: top -5px left -5px;
  background-size: 102.2% auto;
  padding-top: 1.535rem; }

.row-header {
  height: 48.5rem;
  background-image: url(../img/images/foto.png);
  background-repeat: no-repeat;
  background-position: top 5px right -6.625rem;
  background-size: 81% auto; }

.header__topLine {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 7.6rem;
  padding-right: 0.3rem; }

.logo {
  background-image: url(../img/images/Логотип.png);
  background-repeat: no-repeat;
  height: 3.3125rem;
  width: 3.9375rem; }

/* Кнопка меню-гамбургер
------------------------------------------------*/
.burger {
  z-index: 10000;
  padding: 0.3rem;
  border: none;
  border-radius: 0.2rem;
  -webkit-border-radius: 0.2rem;
  -moz-border-radius: 0.2rem;
  -ms-border-radius: 0.2rem;
  -o-border-radius: 0.2rem;
  background: none;
  cursor: pointer; }
  .burger:hover {
    background: #91aa59; }
  .burger__line {
    display: block;
    width: 23px;
    height: 2px;
    margin-bottom: 5px;
    border-radius: 1px;
    background: #ffffff; }
    .burger__line:last-child {
      margin-bottom: 0; }
  .burger__line:first-child {
    width: 0.9375rem; }

/*----------------------------------------------*/
.phone {
  opacity: 0.87;
  color: #000000;
  font-family: "SFUITextRegular";
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.31;
  text-transform: uppercase;
  text-decoration: none; }

.header__title {
  margin: 0;
  color: #ffffff;
  font-family: "RobotoRegular";
  font-size: 3.55rem;
  font-weight: 200;
  line-height: 1.24;
  letter-spacing: 0.273rem;
  text-transform: uppercase; }

.header__desc {
  margin-top: 1.2rem;
  margin-bottom: 2.825rem;
  color: #ffffff;
  font-family: "SFUITextRegular";
  font-size: 1.125rem;
  font-weight: 200;
  line-height: 1.7; }

/* Кнопка со стрелкой основная 
/*----------------------------------------------*/
.btn {
  position: relative;
  display: inline-block;
  text-align: center;
  line-height: 3.375;
  padding: 0 46px;
  border-radius: 1.688rem;
  border: 0.125rem solid #ffffff;
  text-decoration: none; }
  .btn__text {
    display: inline;
    color: #ffffff;
    font-family: "SFUITextRegular";
    font-size: 0.875rem;
    font-weight: 500; }
  .btn__arrow {
    position: relative;
    width: 4.5rem;
    height: 3.375rem; }

.btn__arrowBase,
.btn__arrowBase::after,
.btn__arrowBase::before {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -1px;
  content: '';
  width: 4.5rem;
  height: 0.125rem;
  background-color: #ffffff; }

.btn__arrowBase::after {
  width: 0.4375rem;
  transform: translateY(6px);
  transform: rotate(40deg);
  transform-origin: right top; }

.btn__arrowBase::before {
  width: 0.4375rem;
  transform: translateY(-6px);
  transform: rotate(-40deg);
  transform-origin: right bottom; }

.wrapper__arrow {
  position: absolute;
  right: -2.25rem;
  top: 0%; }

/*============================================= */
/*=============================================
        ОСНОВНОЙ КОНТЕНТ
============================================= */
.main {
  margin-top: 4.975rem; }

/*---------------------------------------------
  Секция о пилатесе
--------------------------------------------- */
.row-base {
  min-height: 25rem;
  background-repeat: no-repeat;
  background-position: top 87% right 57.5%; }

.title-section {
  padding-left: 2.6rem;
  opacity: 0.87;
  color: #000000;
  font-family: "RobotoRegular";
  font-size: 3rem;
  font-weight: 400;
  overflow: hidden; }
  .title-section span {
    display: inline-block;
    vertical-align: middle;
    text-align: center; }
  .title-section:before, .title-section:after {
    content: '';
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    height: 3px;
    background-color: #000000; }
  .title-section:before {
    left: -0.2%;
    margin-left: -100%; }
  .title-section:after {
    right: -0.2%;
    margin-right: -100%; }
  .title-section::before {
    left: -0.325rem; }
  .title-section::after {
    display: none; }

.desc-section {
  opacity: 0.87;
  color: #000000;
  font-family: "SFUITextRegular";
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5; }

.next {
